import React, { useEffect, useRef, useState } from 'react';
import { Button, Form, Input, message, Modal, Table } from 'antd';
import type { ColumnsType } from 'antd/es/table';
import { DeleteOutlined, ExclamationCircleOutlined, EditOutlined } from '@ant-design/icons';
import { connect } from 'react-redux';
import { Dispatch, bindActionCreators } from 'redux';
import * as BlogActions from '@/redux/actionCreator';
import MyPagination from '@/components/pagination';
import dayjs from 'dayjs';
import jwtDecode from 'jwt-decode';
import UploadImage from '@/components/uploadMany';
import Editor from 'for-editor';
import { handleNotAdd, handleNotDelete, handleNotUpdate } from '@/utils/prompt';
const { confirm } = Modal;
const { Search } = Input;
interface CoverData {
  url: string;
  name: string;
  thumbUrl: string;
}
interface DataType {
  key: React.Key;
  _id: string;
  content?: string;
  createTime: string;
  updateTime: string;
}
interface EssayData {
  cover: string;
  totalCount: number;
  page: number;
  pageSize: number;
  data: DataType[];
}
const Essay = (props: any) => {
  const token = jwtDecode(localStorage.getItem('token') as string) as object | any;
  const role_type = token[0].role[0].role_type
  const columns: ColumnsType<DataType> = [
    {
      title: 'éšç¬”',
      dataIndex: 'content',
      render: (_, record) => {
        return <p className='introduction' style={{ width: '40rem' }}>{record.content}</p>;
      },
    },
    {
      title: 'åˆ›å»ºæ—¶é—´',
      dataIndex: 'createTime',
      render: time => {
        return dayjs(time * 1000).format('YYYY-MM-DD HH:mm:ss');
      },
    },
    {
      title: 'æ›´æ–°æ—¶é—´',
      dataIndex: 'updateTime',
      render: time => {
        return time === 0 ? time : dayjs(time * 1000).format('YYYY-MM-DD HH:mm:ss');
      },
    },
    {
      title: 'æ“ä½œ',
      key: 'action',
      className: 'handle_btn',
      render: item => {
        return (
          <div>
            <Button
              type="primary"
              danger
              shape="circle"
              icon={<DeleteOutlined />}
              onClick={() => {
                essayDelete(item);
              }}
              style={{ marginRight: '5px' }}
            />
            <Button
              type="primary"
              ghost
              shape="circle"
              icon={<EditOutlined />}
              onClick={() => {
                essayUpdate(item);
              }}
              style={{ marginRight: '5px' }}
            />
          </div>
        );
      },
    },
  ];
  // è¡¨å•æ•°æ®
  const [form] = Form.useForm();
  // æ›´æ–°è¡¨å•
  const [updateForm] = Form.useForm();
  // éšç¬”åˆ—è¡¨
  const [list, setList] = useState<DataType[]>([]);
  // åˆ†é¡µæ€»æ•°
  const [total, setTotal] = useState(0);
  // å½“å‰ç¬¬å‡ é¡µ
  const [currentPage, setCurrentPage] = useState(1);
  // æ¯é¡µæ˜¾ç¤ºæ¡æ•°
  const [pageSize, setPageSize] = useState(15);
  // çª—å£
  const [isModalOpen, setIsModalOpen] = useState(false);
  // æ›´æ–°çª—å£
  const [isModalUpdateOpen, setIsModalUpdateOpen] = useState(false);
  // å›¾ç‰‡
  const [imageList, setImageList] = useState<any>([]);
  // å›¾ç‰‡åœ°å€
  const [imgUrl, setImgUrl] = useState<any>([]);
  // ä¿å­˜å½“å‰æ›´æ–°çš„æ•°æ®
  const [editData, setEditData] = useState({});
  // å®šä¹‰ref
  const editorRef = useRef<any>();
  // è·å–éšç¬”åˆ—è¡¨æ•°æ®
  useEffect(() => {
    props.BlogActions.asyncEssayListAction(currentPage, pageSize, '').then((res: EssayData) => {
      // è·å–éšç¬”
      let { data, totalCount, page, pageSize } = res.data as unknown as EssayData;
      setList(data);
      setTotal(totalCount);
      setCurrentPage(page);
      setPageSize(pageSize);
    });
  }, [currentPage, pageSize, props.BlogActions]);

  // æ·»åŠ éšç¬”
  const showModal = () => {
    setIsModalOpen(true);
  };
  // ç‚¹å‡»ç¡®å®šæŒ‰é’®æ·»åŠ 
  const handleConfirm = async () => {
    if (role_type) {
      return handleNotAdd()
    }
    // æ ¡éªŒformå€¼ æ ¡éªŒé€šè¿‡åè·å–å€¼
    await form.validateFields();
    // è·å–è¡¨å•å€¼
    const datas = form.getFieldsValue();

    datas.cover = imageList

    let coverArray = datas.cover.map((item: CoverData) => {
      return {
        name: item.name,
        thumbUrl: item.thumbUrl
      }
    })
    let newData = {
      content: datas.content,
      cover: coverArray
    }

    props.BlogActions.asyncEssayInsertAction({
      ...newData,
    }).then(() => {
      message.success('æ·»åŠ éšç¬”æˆåŠŸ!')
      setImageList('')
      form.resetFields();
      setIsModalOpen(false);
      // é‡æ–°è°ƒç”¨æŸ¥è¯¢æ¥å£
      props.BlogActions.asyncEssayListAction(currentPage, pageSize, '').then((res: EssayData) => {
        let { data } = res.data as unknown as EssayData;
        setList(data);
      });
    });
  };
  // å…³é—­çª—å£
  const handleCancel = () => {
    form.resetFields();
    setIsModalOpen(false);
  };
  // å…³é—­çª—å£
  const handleUpdateCancel = () => {
    updateForm.resetFields();
    setIsModalUpdateOpen(false);
  };
  // æ›´æ–°
  const essayUpdate = (item: EssayData) => {
    setImageList(item)
    setImgUrl(item.cover);
    setIsModalUpdateOpen(true);
    updateForm.setFieldsValue(item);
    setEditData(item);
  };
  // æäº¤æ›´æ–°
  const handleUpdateConfirm = () => {
    if (role_type) {
      return handleNotUpdate();
    }
    let data = updateForm.getFieldsValue();
    let coverArray = imageList.cover.map((item: CoverData) => {
      return {
        name: item.name,
        thumbUrl: item.thumbUrl
      }
    })
    let newData = {
      content: data.content,
      cover: coverArray
    }

    props.BlogActions.asyncEssayUpdateAction({
      ...newData,
      //@ts-ignore
      id: editData._id,
    }).then(() => {
      message.success('æ›´æ–°æˆåŠŸ');
      props.BlogActions.asyncEssayListAction(currentPage, pageSize, '').then((res: EssayData) => {
        // è·å–éšç¬”
        let { data, totalCount, page, pageSize } = res.data as unknown as EssayData;
        setList(data);
        setTotal(totalCount);
        setCurrentPage(page);
        setPageSize(pageSize);
      });
      updateForm.resetFields();
      setIsModalUpdateOpen(false);
    });
  };
  // åˆ é™¤éšç¬”
  const essayDelete = (item: DataType) => {
    confirm({
      title: 'ä½ ç¡®å®šè¦åˆ é™¤å—?',
      icon: <ExclamationCircleOutlined />,
      onOk() {
        if (role_type) {
          return handleNotDelete();
        }
        // å…ˆå°†è¦åˆ é™¤çš„æ•°æ®è¿‡æ»¤æ‰å†è°ƒç”¨æ¥å£
        setList(list.filter((it) => it._id !== item._id));
        message.success('éšç¬”åˆ é™¤æˆåŠŸ');
        props.BlogActions.asyncEssayDeleteAction(item._id).then(() => {
          props.BlogActions.asyncEssayListAction(currentPage, pageSize, '').then((res: EssayData) => {
            // è·å–éšç¬”
            let { data, totalCount, page, pageSize } = res.data as unknown as EssayData;
            setList(data);
            setTotal(totalCount);
            setCurrentPage(page);
            setPageSize(pageSize);
          });
        });
      },
    });
  };
  // æœç´¢
  const onSearch = (value: string) => {
    props.BlogActions.asyncEssayListAction(currentPage, pageSize, value).then((res: EssayData) => {
      let { data, totalCount, page, pageSize } = res.data as unknown as EssayData;
      setList(data);
      setTotal(totalCount);
      setCurrentPage(page);
      setPageSize(pageSize);
    });
  };
  // è·³è½¬é¡µæ•°æ®æ˜¾ç¤º
  const onChangePage = (page: number, pageSize: number, params = '') => {
    // é‡æ–°è°ƒç”¨æ¥å£å°†å‚æ•°ä¼ é€’è¿‡å»
    props.BlogActions.asyncEssayListAction(page, pageSize, params).then((res: EssayData) => {
      // è·å–åˆ—è¡¨æ•°æ®
      let { data } = res.data as unknown as EssayData;
      setList(data);
      // åˆ‡æ¢è¡Œ
      setCurrentPage(page);
      // æ ¹æ®é¡µé¢æ•°æ®æ˜¾ç¤ºé¡µç 
      setPageSize(pageSize);
    });
  };

  // è·å–å›¾ç‰‡ä¿¡æ¯
  const handleChange = (data: CoverData) => {
    let newData = {
      name: data.name,
      thumbUrl: data.url
    }
    imageList?.cover ? imageList.cover.push(newData) : imageList.push(newData)
    setImageList(imageList)
  };
  // è·å–ç§»é™¤çš„å›¾ç‰‡ä¿¡æ¯
  const handleRemove = (val: { name: string }) => {
    let newImageList = imageList?.cover ? imageList.cover.filter((item: { name: string }) => item.name !== val.name) : imageList.filter((item: { name: string }) => item.name !== val.name)
    // imageList.push('')
    setImageList({
      ...imageList,
      cover: newImageList
    })
  }
  // æ·»åŠ å›¾ç‰‡
  const addImg = (file: any) => {
    const formData = new FormData();
    formData.append('file', file);
    // ä¸Šä¼ å›¾ç‰‡æ¥å£
    props.BlogActions.asyncFileUploadAction(formData).then((res: CoverData) => {
      if (res) {
        // å¦‚æœè¿”å›å€¼
        editorRef.current.$img2Url(file.name, res.url);
      }
    });
  };
  return (
    <div>
      <div className="title">
        <Button type="primary" onClick={showModal} className="btn">
          æ·»åŠ éšç¬”
        </Button>
        <Search
          className="search"
          allowClear
          placeholder="è¯·è¾“å…¥æƒ³è¦æŸ¥è¯¢çš„éšç¬”"
          onSearch={onSearch}
          enterButton
        />
      </div>
      <Modal
        open={isModalOpen}
        className='modal-essay'
        title={<div style={{ textAlign: 'left' }}>æ·»åŠ éšç¬”</div>}
        okText="æ·»åŠ "
        cancelText="å–æ¶ˆ"
        onCancel={handleCancel}
        onOk={() => {
          handleConfirm();
        }}
      >
        <Form form={form} layout="vertical" name="basic" className="userAddFrom">
          <Form.Item
            name="content"
            // label="éšç¬”"
            rules={[{ required: true, message: 'éšç¬”ä¸èƒ½ä¸ºç©º' }]}
          >
            <Editor
              preview={false}
              subfield={false}
              placeholder="è¯·æ’°å†™éšç¬”å†…å®¹"
              ref={editorRef}
              addImg={file => addImg(file)}
            />
          </Form.Item>
          <Form.Item
            name="cover"
            // label="éšç¬”å›¾ç‰‡"
          >
            {/* @ts-ignore */}
            <UploadImage handleChange={handleChange} handleRemove={handleRemove} />
          </Form.Item>
        </Form>
      </Modal>
      <Modal
        open={isModalUpdateOpen}
        title={<div style={{ textAlign: 'left' }}>æ›´æ–°éšç¬”</div>}
        okText="æ›´æ–°"
        className='modal-essay'
        cancelText="å–æ¶ˆ"
        onCancel={handleUpdateCancel}
        onOk={() => {
          handleUpdateConfirm();
        }}
      >
        <Form form={updateForm} layout="vertical" name="basic" className="userAddFrom">
          <Form.Item
            name="content"
            // label="éšç¬”"
            rules={[{ required: true, message: 'éšç¬”ä¸èƒ½ä¸ºç©º' }]}
          >
            <Editor
              preview={false}
              subfield={false}
              placeholder="è¯·æ’°å†™éšç¬”å†…å®¹"
              ref={editorRef}
              addImg={file => addImg(file)}
            />
          </Form.Item>
          <Form.Item
            name="cover"
            // label="æ–‡ç« å°é¢"
          >
            {/* @ts-ignore */}
            <UploadImage imgUrlArr={imgUrl} handleChange={handleChange} handleRemove={handleRemove} />
          </Form.Item>
        </Form>
      </Modal>
      <Table
        columns={columns}
        dataSource={list}
        scroll={{ y: 'calc(100vh - 240px)', x: true }}
        rowKey={(item) => {
          return item._id + Date.now();
        }}
        pagination={false}
      />
      <MyPagination
        pageSize={pageSize}
        currentPage={currentPage}
        total={total}
        onChange={onChangePage}
      ></MyPagination>
    </div>
  );
};

const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    BlogActions: bindActionCreators(BlogActions, dispatch),
  };
};
export default connect(null, mapDispatchToProps)(Essay);
